@import url(https://fonts.googleapis.com/css2?family=Fraunces&display=swap);

.cursor {
    width: 2.7vw;
    height: 2.7vw;
    border: 0.2vw solid #fefefe;
    border-radius: 100%;
    position: fixed;
    transform: translate(-50%, -50%);
    pointer-events: none;
    z-index: 1000;
    mix-blend-mode: difference;
    transition-property: opacity, background-color, transform, mix-blend-mode;
}

.cursor--clicked {
    transform: translate(-50%, -50%) scale(0.9);
    background-color: #fefefe;
}

.cursor--link-hovered {
    transform: translate(-50%, -50%) scale(1.25);
    background-color: #fefefe;
}
#navbar {
    width: 100%;
    display: inline;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100;
    background: transparent;
}

#navbar ul {
    width: 100%;
}

#navbar li {
    float: right;
    padding: 1% 1.5% 1% 1.5%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

#navbar a {
    color: white;
    display: inline;
    text-decoration: none;
}

#navbar a:hover {
    color: white;
}

.sticky {
    position: fixed !important;
    background-color: #08090a !important;
    color: black;
}



#skill-content {
    display: inline-block;
    width: 100%;
    height: auto;
}

#skill-content li {
    display: inline-block;
    padding-right: 1%;
    padding-left: 1%;
    width: 12%;
}

#skill-content li p {
    color: black;
    white-space: pre-line;
    word-wrap: break-word;
}

/*
Device = Desktops
Screen = 1281px to higher resolution desktops
*/

@media (min-width: 1281px) {

    #skill-content li {
        width: 11%;
    }

}

/*
Device = Laptops, Desktops
Screen = B/w 1025px to 1280px
*/

@media (min-width: 1025px) and (max-width: 1280px) {


    #skill-content li {
        width: 10%;
    }

    #skill-content li p {
        font-size: 1vw;
    }
}

/*
Device = Tablets, Ipads (portrait)
Screen = B/w 768px to 1024px
*/

@media (min-width: 768px) and (max-width: 1024px) {

    #skill-content li {
        width: 10%;
    }

    #skill-content li p {
        font-size: 2vw;
    }
}

/*
Device = Tablets, Ipads (landscape)
Screen = B/w 768px to 1024px
*/

@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {

    #skill-content li {
        width: 12%;
    }

    #skill-content li p {
        font-size: 2vw;
    }

}


/*
Device = Low Resolution Tablets, Mobiles (portrait)
Screen = B/w 600px to 800px
*/

@media (min-width: 600px) and (max-width: 800px) and (orientation: portrait) {

    #skill-content li {
        width: 15%;
    }

    #skill-content li p {
        font-size: 2vw;
    }

}

/*
Device = Low Resolution Tablets, Mobiles (Landscape)
Screen = B/w 481px to 767px
*/

@media (min-width: 481px) and (max-width: 767px) and (orientation: landscape) {

    #skill-content li {
        width: 12%;
    }

    #skill-content li p {
        font-size: 2vw;
    }
}

/*
 Device = Most of the Smartphones Mobiles (Portrait)
 Screen = B/w 320px to 479px
*/

@media (min-width: 320px) and (max-width: 480px) {

    #skill-content li {
        width: 15%;
    }

    #skill-content li p {
        font-size: 2.5vw;
    }
}



#canvas{
    width: 100%;
    height: 100vh;
}
@media (min-width: 481px) and (max-width: 767px) {
    #content {
        font-size: 3vw;
    }
}

/*
  ##Device = Most of the Smartphones Mobiles (Portrait)
  ##Screen = B/w 320px to 479px
*/

@media (min-width: 320px) and (max-width: 480px) {

    #content {
        font-size: 4vw;
    }

}
* {
    margin: 0;
    padding: 0;
    cursor: none;
    color: white;
    -webkit-text-size-adjust: auto;
    font-family: 'Fraunces', serif !important;
}

html, body {
    height: 100%;
}

hr {
    padding: 0.005%;
    background-color: red;
    border: 0.05vw solid red;
}

#root {
    height: 100%;
}

.bg-black {
    background-color: #0a0a0a;
}

.bg-white {
    background-color: #f5f5f5;
}

::-webkit-scrollbar {
    width: 0;
    height: 0;
    background: transparent; /* make scrollbar transparent */
}


